<style scoped>
   
</style>
<template>
  <div>
    <Table :columns="columns" :data="data" style="margin:2px 80px"></Table>
    <Form :model="formItem" :label-width="200">
    <FormItem label="日期控件" style="width:60%">
            <Row>
                <Col span="11">
                    <DatePicker type="date" placeholder="选择日期" v-model="formItem.date"></DatePicker>
                </Col>         
            </Row>
        </FormItem>
        <FormItem label="代号" style="width:50%">
            <Input v-model="formItem.name" placeholder="请输入"></Input>
        </FormItem>
        
        <FormItem label="单选">
            <RadioGroup v-model="formItem.gender">
                <Radio label="male">男</Radio>
                <Radio label="female">女</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="留言" style="width:50%">
            <Input v-model="formItem.trace" type="textarea" :autosize="{minRows: 5,maxRows: 7}" placeholder="请输入..."></Input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="save">Save</Button>
        </FormItem>
    </Form>
    </div>
</template>
<script>
    export default {
        data () {
            return {
            	formItem: {
                    name: '',
                    gender: 'male',
                    date: '',
                    trace: ''
                },
                columns: [
                    {
                        title: '日期',
                        key: 'date',
                        width:150,
                        sortable: true
                    },
                    {
                        title: '代号',
                        width:150,
                        key: 'name'
                    },
                    {
                        title: '性别',
                        key: 'gender',
                        width:100

                    },
                    {
                        title: '留言',
                        key: 'trace'
                    }
                ],
                data: [
                    {
                        name: 'LDL',
                        gender: "female",
                        trace: '大家好！我是本站的菜鸟作者。发布这个项目是因为想自己开发维护一个属于自己的网站，现在的所有都是一个雏形，因为迫不及待想试试上线所以没有系统的设计过，后续会更新完善，欢迎大家体验交流，提意见！',
                        date: '2017-9-10'
                    },
                    {
                        name: 'Inneil',
                        gender: "female",
                        trace: '这个还是我啦，关于这个网站拥有什么信息价值，我想说的是：第一、我会分享好文章，不限于网络技术方面，大家一起学习，一起开车！第二，作为一名立志成为没有程序员气质的同学，我走过一些弯路，希望和大家一起填坑。第三、想认识更多有趣的朋友啦！先留个号xxxxxxxxx；',
                        date: '2017-9-10'
                    },
                    {
                        name: 'Inneil',
                        gender: "female",
                        trace: '留言暂时无法储存，只能本地预览，所以这一切暂时是我单方面交互，等我学习了数据库我会完善的！',
                        date: '2017-9-10'
                    }
                ]
            }
        },
        methods:{
        	save(){
        		this.a = this.formItem
        		this.data.push(this.a)
        		this.formItem = ""
        	}
        }
    }
</script>


